<template>
    <v-container fluid class="container" @click="timer_logout_set()">
        <layout-header/>
        <v-row class="contents" align="center" justify="center">
            <v-col align="center">
                <list-program-list
                    class="content-body"
                    :title="programTitle"
                    :programList="programList"
                />
                <v-btn 
                    color="#fff5a2" 
                    width="300" 
                    height="120" 
                    class="button_start" 
                    to="ProgramPlay"
                >
                    <v-icon size="55" class="mr-3">play_arrow</v-icon>
                    운동시작
                </v-btn>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import timer_logout from '@/mixins/timer_logout.js'

export default {
    name: 'Program',
    mixins:[ timer_logout ],
    data:()=>({
        programTitle: null,
        programList: []
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_SELECT_EXERCISE',
            'GET_MENU_SELECT_MENU',
        ])
    },
    watch:{
    },
    mounted() {
        // this.$route.query.id
        this.EditProgram()
        this.EditRestTime()
    },
    methods: {
        ...mapMutations([
            'MU_PROGRAM_PLAY_REST_SET',
        ]),
        EditProgram(){
            if(this.GET_MENU_SELECT_EXERCISE !== null){
                this.programTitle = this.GET_MENU_SELECT_EXERCISE.name
                this.programList = this.GET_MENU_SELECT_EXERCISE.program
            }else{
                this.$router.go(-1)
            }
        },
        EditRestTime(){
            let restTime = 0
            if(this.GET_MENU_SELECT_MENU === '스트레칭'){
                restTime = 5
                this.MU_PROGRAM_PLAY_REST_SET(restTime)
            }else{
                restTime = 20
                this.MU_PROGRAM_PLAY_REST_SET(restTime)
            }
        }
    }
}
</script>

<style scoped lang="scss">
    .button_start {
        font-size: 36px;
        margin-top: 8rem;
    }
</style>